import {Component, OnInit} from '@angular/core';
import {FootballService} from "./service/football.service";
import {StreamLinks} from "../models/stream-links";
import {Observable} from "rxjs/index";

@Component({
  selector: 'app-football',
  templateUrl: './football.component.html',
  styleUrls: ['./football.component.css']
})
export class FootballComponent implements OnInit {
  streamLinksObs: Observable<StreamLinks[]>;
  streamLinks: StreamLinks[];
  haveData: Boolean = null;

  constructor(private footballService: FootballService) {
  }

  ngOnInit() {
    this.streamLinksObs = this.footballService.getStreamLinksFromAPI();
    console.log(this.streamLinks);
    this.streamLinksObs.subscribe(data => {
        console.log(data.length);
        if (data.length != null) {
          this.streamLinks = data;
          this.haveData = true;
        } else {
          this.haveData = false;
        }
        console.log(this.streamLinks);
      }, err => {
        console.error(err);
      }, () => {
      }
    )
  }

}
