import {Injectable} from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {StreamLinks} from "../../models/stream-links";
import {Observable} from "rxjs/index";

@Injectable({
  providedIn: 'root'
})
export class FootballService {
  resultLinks: StreamLinks[];

  constructor(private  httpClient: HttpClient) {
  }

  public getStreamLinksFromAPI(): Observable<StreamLinks[]> {
    return this.httpClient.get<StreamLinks[]>("/football/get-stream-list-praw");
  }


}
