import {Component, OnInit} from "@angular/core";
import {NbaService} from "./service/nba.service";
import {StreamLinks} from "../models/stream-links";
import {Observable} from "rxjs/index";

@Component({
  selector: 'app-nba',
  templateUrl: './nba.component.html',
  styleUrls: ['./nba.component.css']
})
export class NbaComponent implements OnInit {
  streamLinksObs: Observable<StreamLinks[]>;
  streamLinks: StreamLinks[] = null;
  haveData: Boolean = null;

constructor(private nbaService: NbaService) {
  }

  ngOnInit() {
    this.streamLinksObs = this.nbaService.getStreamLinksFromAPI();
    console.log(this.streamLinks);
    this.streamLinksObs.subscribe(data => {
         if (data.length != null) {
          this.streamLinks = data;
          this.haveData = true;
        } else {
          this.haveData = false;
        }
      }, err => {
        console.error(err);
      },() => {
      console.log(this.haveData)
      }
    )
  }

}
