import { Injectable } from '@angular/core';
import {StreamLinks} from "../../models/stream-links";
import {Observable} from "rxjs/index";
import {HttpClient} from "@angular/common/http";

@Injectable({
  providedIn: 'root'
})
export class NbaService {

  constructor(private  httpClient: HttpClient) {
  }

  public getStreamLinksFromAPI(): Observable<StreamLinks[]> {
    return this.httpClient.get<StreamLinks[]>("/nba/get-stream-list-praw");
  }

}
